export default {
	data() {
		return {
			canSee1: false,
			canSee2: false,
			mobile: '',
			code: '',
			state: false, //是否开启倒计时,
			time: 70, //总时间，单位秒
			getCodeTxt: '获取验证码',
			showChangePassword: false,
			newPassword1: '',
			newPassword2: '',
			sendSuccess: false
		}
	},
	methods: {
		sureChange() {
			if (this.newPassword1 == '') {
				this.showToast('请输入新密码')
				return
			} else if (this.newPassword2 == '') {
				this.showToast('请确认新密码')
				return
			} else if (this.newPassword1 != this.newPassword2) {
				this.showToast('两次密码不一致')
				return
			}
			uni.showLoading({
				title:'请稍等'
			})
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/user/editPwd',
				data: {
					phone: this.mobile,
					active_code: this.code,
					new_password: this.newPassword1,
					new_password2: this.newPassword2
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					uni.hideLoading()
					if (res.data.status == 0) {
						uni.navigateBack({
							delta: 1,
							success: () => {
								uni.showToast({
									title: '修改成功',
									icon: 'success'
								})
							}
						})
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		changeSee1() {
			this.canSee1 = !this.canSee1
		},
		changeSee2() {
			this.canSee2 = !this.canSee2
		},
		toLogin() {
			uni.redirectTo({
				url: "./login"
			})
		},
		//点击获取验证码
		getCode() {
			if (this.mobile == '') {
				this.showToast('请输入手机号码')
				return
			}
			uni.showLoading({
				title:'请稍等'
			})
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/User/sendActiveCode',
				data: {
					phone: this.mobile
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					uni.hideLoading()
					if (res.data.status == 0) {
						this.showToast('验证码已发送')
						this.sendSuccess = true
						//开始倒计时
						this.state = true;
						//执行倒计时
						this.checkingTime();
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		//倒计时
		checkingTime() {
			var _this = this
			var timer = setInterval(function() {
				_this.time--
				if (_this.time <= 0) {
					clearInterval(timer)
					_this.state = false;
					_this.getCodeTxt = '重新获取'
					_this.time = 70
				}
			}, 1000)
		},
		toNext() {
			if (this.mobile == '') {
				this.showToast('输入账号不能为空')
				return
			} else if (this.code == '') {
				this.showToast('验证码不能为空')
				return
			}
			if (!this.sendSuccess) {
				return
			}
			this.showChangePassword = true
		}
	}
}
